html {
  font-size: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Space Grotesk", sans-serif;
  color: hsl(0deg, 0%, 85%);
  background-color: hsl(0deg, 0%, 8%);
  position: relative;
  overflow-x: hidden;
}

ol,
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  width: min(69.375rem, 100% - 3.75rem);
  position: relative;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1;
  font-weight: 700;
  color: hsl(0deg, 0%, 100%);
}

h1 {
  font-size: clamp(1.5rem, 1rem + 1.5vw, 2rem);
}

h2 {
  font-size: 2.5rem;
  letter-spacing: -0.15625rem;
}
@media (min-width: 40em) {
  h2 {
    font-size: 4.5rem;
  }
}
@media (min-width: 64em) {
  h2 {
    font-size: 5.5rem;
  }
}

h3 {
  font-size: 1.5rem;
}

p {
  margin: 0;
  color: hsl(0deg, 0%, 85%);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.button {
  border: none;
  cursor: pointer;
  font-weight: 700;
  font-size: 1rem;
  max-width: fit-content;
  letter-spacing: 0.125rem;
  padding-bottom: 0.625rem;
  color: hsl(0deg, 0%, 100%);
  text-transform: uppercase;
  background-color: transparent;
  border-bottom: 0.125rem solid hsl(153deg, 71%, 59%);
  transition: color 150ms linear;
}
.button:is(:hover, :focus-visible) {
  color: hsl(153deg, 71%, 59%);
}

.header {
  display: grid;
  place-items: center;
  gap: 1.25rem;
  padding-top: 1.25rem;
}
@media (min-width: 40em) {
  .header {
    grid-auto-flow: column;
    justify-content: space-between;
    padding-top: 2.5rem;
  }
}
.header__social-list {
  display: flex;
  gap: 1.25rem;
  margin-right: 1.875rem;
}
.header .icon {
  height: 1.5rem;
  width: 1.5rem;
  fill: hsl(0deg, 0%, 100%);
}
.header .icon:is(:hover, :focus-visible) {
  fill: hsl(153deg, 71%, 59%);
}

.hero {
  display: grid;
  place-items: center;
  text-align: center;
  gap: 1.25rem;
  padding-bottom: 2.5rem;
}
.hero__content {
  display: grid;
  gap: 1.25rem;
  justify-items: center;
}
@media (min-width: 40em) {
  .hero {
    grid-template-areas: "content photo";
    text-align: left;
  }
  .hero__content {
    grid-area: content;
    justify-items: start;
    gap: 2.5rem;
  }
  .hero__title {
    width: max(120%, 10ch);
  }
  .hero__subtitle {
    max-width: 35ch;
  }
}
.hero__image {
  width: 10.9375rem;
  position: relative;
  grid-area: photo;
  margin-top: -5.8125rem;
  z-index: -2;
}
@media (min-width: 40em) {
  .hero__image {
    width: 20.125rem;
  }
}
@media (min-width: 64em) {
  .hero__image {
    width: 28.125rem;
  }
}
.hero__title span {
  display: inline-block;
  border-bottom: 0.375rem solid hsl(153deg, 71%, 59%);
}
.hero::before {
  content: url("../../../images/pattern-rings.be49833a.svg");
  position: absolute;
  top: 10%;
  left: -16.5625rem;
  z-index: -3;
}
.hero::after {
  content: url("../../../images/pattern-circle.ad1ffe0a.svg");
  position: absolute;
  bottom: 50%;
  right: -6.25rem;
  z-index: -1;
}
@media (min-width: 40em) {
  .hero::after {
    bottom: 0;
  }
}
@media (min-width: 64em) {
  .hero::after {
    bottom: 10%;
    right: 35%;
  }
}

.skill {
  padding: 2.5rem 0;
  border-block: 1px solid hsl(0deg, 0%, 100%);
}
@media (min-width: 40em) {
  .skill {
    border-bottom: none;
  }
}
.skill__list {
  display: grid;
  justify-items: center;
  text-align: center;
  gap: 1.5rem;
}
@media (min-width: 40em) {
  .skill__list {
    grid-template-columns: repeat(2, 1fr);
    justify-items: start;
    text-align: left;
    row-gap: 2.5rem;
  }
}
@media (min-width: 64em) {
  .skill__list {
    grid-template-columns: repeat(3, 1fr);
  }
}
.skill__name {
  color: hsl(0deg, 0%, 100%);
  font-size: 2rem;
  padding-bottom: 0.625rem;
}
@media (min-width: 40em) {
  .skill__name {
    font-size: 3rem;
  }
}
@media (min-width: 40em) {
  .skill__experience {
    font-size: 1.125rem;
  }
}
.skill::before {
  content: url("../../../images/pattern-rings.be49833a.svg");
  position: absolute;
  right: -23.75rem;
  bottom: 0;
}